import React, { useState } from 'react';
import ParticleAnimation from './ParticleAnimation'
import Web3 from 'web3';
import Web3Modal from 'web3modal';
import WalletConnect from "@walletconnect/web3-provider";

function Client() {
  const host = '2h6q9xyqrh.execute-api.us-east-2.amazonaws.com';
  // const port = 8081;
  const [address, setAddress] = useState('');
  const [datas, setData] = useState([]);
  const [deletemsg, setDeletmsg] = useState([])
  const [confirmmsg, setConfirmmsg] = useState([])

  console.log(datas)

  const providerOptions = {
    binancechainwallet: {
      package: true,
    },
    walletconnect: {
      package: WalletConnect, 
      options: {
        infuraId: "3a556aa3286c4bb08d020d6d03aaf976", 
      }
    }
  };
  const web3modal = new Web3Modal({
    network: "goerli",
    theme: "dark",
    cacheProvider: true,
    providerOptions
  });

  const connectWallet = async () => {
    var provider = await web3modal.connect();
		var web3 = new Web3(provider);
		await window.ethereum.send('eth_requestAccounts');
	
		var accounts = await web3.eth.getAccounts();
		const account = accounts[0];
    setAddress(account)
  }
  //Check Address Function
  const check = async (address) => {
    try {
      const response = await fetch(`https://${host}/api/client/${address}`);
      const jsonData = await response.json();
      setData(jsonData)
    } catch (err) {
      console.error(err.message)
    }
  }
  //Delete Address Function
  const deleteAddress = async (address) => {
    try {
        const deletePay = await fetch(`https://${host}/api/client/${address}`, {
            method: "DELETE"
        });
        const jsonData = await deletePay.json();
        setDeletmsg(jsonData)
    } catch (err) {
        console.error(err.message)
    }
  }
  //Confirm Address Function
  const confirmAddress = async (address) => {
    try {
      const confirm = await fetch(`https://${host}/api/client/put/${address}`, {
        method: "PUT"
      });
      const jsonData = await confirm.json()
      setConfirmmsg(jsonData)
    } catch (err) {
      console.error(err.message)
    }
  }
  
  return (
    <div style={{padding: '0', margin: '0'}}>
        <div><ParticleAnimation/></div>
      <div 
        style={{borderRadius: '15px' ,position:'absolute', opacity: '0.7', top: '50px', height:'500px', width: '650px', margin: 'auto 31%', marginTop: '100px', background: "#75F7FB", boxShadow: "-1px 3px 40px 28px rgba(97,235,235,1)"}} 
        className='card'>
        {!address && <button style={{margin: 'auto', width: '150px', opacity: '1'}} className='btn btn-primary mt-4 mb-2' onClick={connectWallet}>Connect</button>}
        {!address &&<label style={{width: '80%', textAlign: 'center', boxShadow: "0px 0px 10px 1px rgba(0,0,0,1)", margin: '15px auto'}}>Please Connect Your Wallet</label>}
        {address && <button style={{margin: 'auto', width: '150px'}} className='btn btn-primary mt-4 mb-2' onClick={connectWallet}>Connected</button>}
        {address && <label style={{width: '80%', textAlign: 'center', boxShadow: "0px 0px 10px 1px rgba(0,0,0,1)", margin: '15px auto'}}>{address}</label>}
        <button style={{margin: 'auto', width: '150px'}} className='btn btn-primary mt-2' onClick={() => check(address)}>Check</button>
        {datas !== 'You are not on any List!.' && datas.map(data => {
          let delet, confirm
          if(data.type === 'FYFList' && String(data.confirm) === 'false'){
            delet = <button onClick={() => deleteAddress(data.address)} style={{margin: 'auto', width: '150px'}} className='btn btn-danger mt-2 mb-2'>Delete</button>
            confirm = <button onClick={() => confirmAddress(data.address)} style={{margin: 'auto', width: '150px'}} className='btn btn-success mt-2 mb-2'>Confirm</button>
          }else if(data.type === 'FYFList' && String(data.confirm) === 'true'){
            delet = <button onClick={() => deleteAddress(data.address)} style={{margin: 'auto', width: '210px'}} className='btn btn-danger mt-2 mb-2'>Delete</button>
            confirm = ""
          }
          let Confirmed
          if(data.type === 'FYFList' && String(data.confirm) === 'false'){
            Confirmed = <label style={{margin: 'auto', color: 'red'}}>Your Address Is Not Confirm</label>
          }else if(data.type === 'FYFList' && String(data.confirm) === 'true'){
            Confirmed = <label style={{margin: 'auto', color: 'green'}}>Your Address Is Confirmed</label>
          }
          let messamge
          if(data.type === 'FYFList'){
            messamge = <span style={{margin: 'auto', color: 'green'}}>
                          Congrats! You are on the FYFList :D<br/>
                          Please confirm your wallet if you are sure that you want to be part of FYF Family before the time is up!<br/>
                          Your wallet will be automatically transferred to FYFExtra in case you didn't confirm!<br/>
                          You can remove your wallet from FYFList if you don't want to mint,
                          so we can replace your wallet with someone that want to be part of FYF!<br/>
                      </span>
          }else if(data.type === 'FYFExtra'){
            messamge = <span style={{margin: 'auto', color: 'green'}}>
                          Congrats! You are on the FYFExtra :)
                      </span>
          }
          return(
            <div style={{padding: '10px', margin: 'auto', color: 'green'}} key={data.id}>
              <div>
                &nbsp;&nbsp;
                <label style={{padding: '10px', margin: 'auto'}}>
                  {messamge}
                </label>
                <br/>
                <span style={{width: '100%', display: 'inline-flex', justifyContent: 'center'}}>
                  {Confirmed}
                </span>
                <br/>
                <br/>
                <span style={{width: '100%', display: 'inline-flex', justifyContent: 'center'}}>
                &nbsp;{delet}&nbsp;
                {confirm}
                </span>
                <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{deletemsg && <label style={{margin: 'auto', color: 'red'}}>{deletemsg}</label>}
                &nbsp;{confirmmsg && <label style={{marginLeft: '-15px',color: 'green'}}>{confirmmsg}</label>}
              </div>
            </div>
          )
        })}
        {datas === 'You are not on any List!.' && <label style={{margin: 'auto', color: 'red'}}>{datas}</label>}
      </div>
    </div>
    
  );
}

export default Client;
