import React, { useEffect, useState } from "react";
import Add from "./Add";
import check from '../check.svg';
import uncheck from '../uncheck.svg';


function Admin() {
    const host = '2h6q9xyqrh.execute-api.us-east-2.amazonaws.com';
    // const port = 8081;
    const [addresses, setAddresses] = useState([])
    const [deletemsg, setDeletmsg] = useState([])


    //Delete Address Function
    const deleteAddress = async (id) => {
        try {
            const deletePay = await fetch(`https://${host}/api/admin/${id}`, {
                method: "DELETE"
            });
            const jsonData = await deletePay.json();
            setAddresses(addresses.filter(address => address.id !== id));
            setDeletmsg(jsonData)
        } catch (err) {
            console.error(err.message)
        }
    }
    //Get All Addresses Function
    const getAll = async () => {
        try {
            const get = await fetch(`https://${host}/api/all`)
            const jsonData = await get.json()
            setAddresses(jsonData)
        } catch (err) {
            console.error(err.message)
        }
    }

    useEffect(() => {
        getAll();
    }, [])

    return(
        <div className="container" style={{zIndex: '1000'}}>
            <h1>Admin Panel</h1>
            {deletemsg && <label style={{color: 'red'}}>{deletemsg}</label>}
            <div>
            <table className="table mt-5 text-center">
                <thead>
                  <tr>
                    <th style={{width: '30px'}}>ID</th>
                    <th style={{width: '400px'}}>Address</th>
                    <th style={{width: '100px'}}>Type</th>
                    <th style={{width: '100px'}}>Confirmed</th>
                    <th style={{width: '100px'}}>Delete</th>
                  </tr>
                </thead>
                <tbody>
                    
                  {addresses.map(address => {
                        let confirm 
                        if(address.type === 'FYFList' && String(address.confirm) === 'false'){
                                confirm = <img width= '37.5px' src={uncheck} alt="uncheck" />
                        }else if(address.type === 'FYFList' && String(address.confirm) === 'true'){
                                confirm = <img width= '37.5px' src={check} alt="check" />
                        }
                    return(
                        <tr key={address.id}>
                            <td>{address.id}</td>
                            <td>{address.address}</td>
                            <td>{address.type}</td>
                            <td>{confirm}</td>
                            <td>
                                <button 
                                className="btn btn-danger"
                                onClick={() => deleteAddress(address.id)}>Delete</button>
                            </td>
                        </tr>
                    )})}
                </tbody>
            </table>
        </div>
            <div style={{float: 'right', marginLeft: '90%', width: '200px'}} className="btn btn-success mt-4"><Add/></div>
        </div>
    )
}

export default Admin;