import React, { useState } from "react";
// import { useNavigate } from "react-router-dom";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';


const Add = () => {
  const host = '2h6q9xyqrh.execute-api.us-east-2.amazonaws.com';
  // const port = 8081;
  // const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const [address, setAddress] = useState('');
  const [type, setType] = useState('');
  const [addMsg, setAddMsg] = useState('')
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const addToList = async () => {
    try {
      const body = { address, type };
      // eslint-disable-next-line
      const response = await fetch(`https://${host}/api`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body)
      })
      const msg = await response.json()
      setAddMsg(msg)
    } catch (err) {
      console.error(err.message)
    }
  }

  return (
    <div>
      <Button variant="success" onClick={handleShow}>
        Add New Address
      </Button>

      <Modal size="lg" show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Add New Address</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <label>
            Address:&nbsp;
          </label>
          <input style={{width: '500px', textAlign: 'center'}} type='text' placeholder="Enter The New Address" onChange={(e) => setAddress(e.target.value)} />
          <br/><br/>
          <label className="mt-2">
            Type:&nbsp;
          </label>
          <select style={{height: '30px', width: '250px', margin: 'auto'}} onChange={(e) => setType(e.target.value)}>
                <option value={""}>Select List Type</option>
                <option value={'FYFList'}>FYFList </option>
                <option value={'FYFExtra'}>FYFExtra</option>
            </select><br/> <br/>
            {addMsg && <label style={{color: 'green'}}>{addMsg}</label>}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            Close
          </Button>
          <Button variant="success" onClick={addToList}>
            ADD To List
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
    )
}

export default Add;