import React from 'react';
import './Nav.css'
import logo from '../logo.png'

const Nav = () => {
  return (
      <div className="navbar">
        <div className='container'>
            <a href='/' className='logo'>
              <img className='mt-0 ' width= '70px' src={logo} alt='' />
            </a>
        </div>
      </div>
  );
}

export default Nav;